@-webkit-keyframes oscillate1 {
  0%,
  100% {
    -webkit-transform: rotate(0) scale(1);
    transform: rotate(0) scale(1);
  }
  33% {
    -webkit-transform: rotate(5deg) scale(0.6);
    transform: rotate(5deg) scale(0.6);
  }
  66% {
    -webkit-transform: rotate(-5deg) scale(1.01);
    transform: rotate(-5deg) scale(1.01);
  }
}

@keyframes oscillate1 {
  0%,
  100% {
    -webkit-transform: rotate(0) scale(1);
    transform: rotate(0) scale(1);
  }
  33% {
    -webkit-transform: rotate(5deg) scale(0.6);
    transform: rotate(5deg) scale(0.6);
  }
  66% {
    -webkit-transform: rotate(-5deg) scale(1.01);
    transform: rotate(-5deg) scale(1.01);
  }
}

@-webkit-keyframes oscillate2 {
  0%,
  100% {
    -webkit-transform: rotate(0) scale(1);
    transform: rotate(0) scale(1);
  }
  50% {
    -webkit-transform: rotate(-10deg) scale(0.8);
    transform: rotate(-10deg) scale(0.8);
  }
}

@keyframes oscillate2 {
  0%,
  100% {
    -webkit-transform: rotate(0) scale(1);
    transform: rotate(0) scale(1);
  }
  50% {
    -webkit-transform: rotate(-10deg) scale(0.8);
    transform: rotate(-10deg) scale(0.8);
  }
}

@-webkit-keyframes oscillate3 {
  0%,
  100% {
    -webkit-transform: rotate(0) scale(1);
    transform: rotate(0) scale(1);
  }
  33% {
    -webkit-transform: rotate(13deg) scale(0.6);
    transform: rotate(13deg) scale(0.6);
  }
  66% {
