  }
  66% {
    -webkit-transform: rotate(-17deg) scale(1.1);
    transform: rotate(-17deg) scale(1.1);
  }
}

@-webkit-keyframes scale {
  0%,
  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
  50% {
    -webkit-transform: scale(0.8);
    transform: scale(0.8);
  }
}

@keyframes scale {
  0%,
